import { definition } from '@eui/component';
import { html, ifNull } from '@eui/lit-component';
import { Panel } from '@eui/panel';
import style from './notificationLogPanel.css';
import './components/notification-log-panel-item/NotificationLogPanelItem';

@definition('eui-notification-log-panel', {
  style,
})
export default class NotificationLogPanel extends Panel {
  constructor() {
    super();
    this._bindMethods();
  }

  /**
   * @private
   * @function _bindMethods
   * @description Bind methods
   */
  _bindMethods() {
    this._readNotification = this._readNotification.bind(this);
    this._removeNotification = this._removeNotification.bind(this);
    this._renderNotification = this._renderNotification.bind(this);
  }

  /**
   * @private
   * @function _readNotification
   * @description Read notification event
   * @param {Object} event - Click event
   */
  _readNotification(event) {
    const { id } = event.detail;

    this.dispatch('READ_NOTIFICATION', id);
    this.bubble('eui-notification:set', this.state['eui:notificationsToRead']);
  }

  /**
   * @private
   * @function _removeNotification
   * @description Remove notification event
   * @param {Object} event - Click event
   */
  _removeNotification(event) {
    const { id } = event.detail;
    this.dispatch('REMOVE_NOTIFICATION', id);
    this.bubble('eui-notification:set', this.state['eui:notificationsToRead']);
  }

  /**
   * @private
   * @function _readStoreNotification
   * @description Read notification
   * @param {number} id - Id of notification to remove
   * @param {object} store - Store
   */
  _readStoreNotification(id, store) {
    const notifications = store.getState('eui:notifications') || [];
    const currentNotification = notifications.find(item => item.id === id);
    if (!currentNotification) {
      return;
    }
    currentNotification.read = true;
    store.setState('eui:notifications', [...notifications]);
    store.setState('eui:notificationsToRead', [...notifications].filter(i => !i.read).length);
  }

  /**
   * @private
   * @function _removeStoreNotification
   * @description Remove notification from notifications log.
   * @param {number} id - Id of notification to remove
   * @param {object} store - Store
   */
  _removeStoreNotification(id, store) {
    const notifications = store.getState('eui:notifications') || [];
    const currentNotification = notifications.find(item => item.id === id);
    if (!currentNotification) {
      return;
    }
    const newNotifications = notifications.filter(item => item.id !== id);
    store.setState('eui:notifications', newNotifications);
    store.setState('eui:notificationsToRead', newNotifications.filter(i => !i.read).length);
  }

  /**
   * @private
   * @function didConnect
   * @description Component did connect
   */
  didConnect() {
    this.state = this.storeConnect(['eui:notifications', 'eui:notificationsToRead']);
    this.addActions('REMOVE_NOTIFICATION', this._removeStoreNotification);
    this.addActions('READ_NOTIFICATION', this._readStoreNotification);
  }

  /**
   * @private
   * @function _renderNotification
   * @description Render notification
   * @param {Object} notification - Notification to render
   * @param {Number} index - Notification to render index
   */
  _renderNotification(notification, index) {
    const cssClass = notification.read ? 'read' : '';
    return html`
      <eui-notification-log-panel-item
        @eui-notification-log-panel-item:read="${this._readNotification}"
        @eui-notification-log-panel-item:remove="${this._removeNotification}"
        class="${cssClass}"
        id="${notification.id}"
        title="${notification.title}"
        description="${notification.description}"
        index="${index}"
        timestamp="${ifNull(notification.timestamp)}"
      ></eui-notification-log-panel-item>
    `;
  }

  /**
   * @private
   * @function _renderNotifications
   * @description Render notifications
   */
  _renderNotifications() {
    return (this.state['eui:notifications'] || []).map(this._renderNotification).reverse();
  }

  /**
   * @private
   * @function render
   * @description Render component
   */
  render() {
    return html`
      ${this._renderNotifications()}
    `;
  }

  /**
   * @private
   * @function didDisconnect
   * @description Component will disconnect
   */
  didDisconnect() {
    this.removeActions('REMOVE_NOTIFICATION', this._removeStoreNotification);
    this.removeActions('READ_NOTIFICATION', this._readStoreNotification);
  }
}
