import { definition } from '@eui/component';
import { LitComponent, html, nothing } from '@eui/lit-component';
import style from './notificationLogPanelItem.css';

@definition('eui-notification-log-panel-item', {
  style,
  props: {
    title: { attribute: true, type: String },
    description: { attribute: true, type: String },
    timestamp: { attribute: true, type: String },
    index: { attribute: true, type: Number },
  },
})
export default class NotificationLogPanelItem extends LitComponent {
  constructor() {
    super();
    this._bindMethods();
  }

  /**
   * @private
   * @description binds the methods
   */
  _bindMethods() {
    this.handleEvent = this.handleEvent.bind(this);
  }

  /**
   * @private
   * @function _calcTimeDiff
   * @description Calculate time of notification
   */
  _calcTimeDiff() {
    const millisecondsInSecond = 1000;
    const secondsInMinute = 60;
    const millisecondsInMinute = secondsInMinute * millisecondsInSecond;
    const secondsInHour = 3600;
    const millisecondsInHour = secondsInHour * millisecondsInSecond;
    const hoursInDay = 24;
    const millisecondsInDay = millisecondsInHour * hoursInDay;

    const dateBefore = new Date(this.timestamp);

    const timeDiff = Math.abs(new Date().getTime() - dateBefore.getTime());
    const dayDiff = Math.floor(timeDiff / millisecondsInDay);
    const hourDiff = Math.floor((timeDiff - dayDiff * millisecondsInDay) / millisecondsInHour);
    const minDiff = Math.floor(
      (timeDiff - dayDiff * millisecondsInDay - hourDiff * millisecondsInHour)
        / millisecondsInMinute,
    );

    const dayDiffString = dayDiff ? `${dayDiff}d` : undefined;
    const hourDiffString = hourDiff ? `${hourDiff}h` : undefined;
    const minDiffString = minDiff ? `${minDiff}m` : undefined;

    return dayDiffString || hourDiffString || minDiffString || 'just now';
  }

  /**
   * @private
   * @function handleEvent
   * @description Handle events
   */
  handleEvent(event) {
    if (event.target === this._dom.crossIconElement) {
      this.bubble('eui-notification-log-panel-item:remove', { id: this.id, index: this.index });
    } else {
      this.bubble('eui-notification-log-panel-item:read', { id: this.id, index: this.index });
    }
  }

  /**
   * @private
   * @function componentDidRender
   * @description Component did render
   */
  didRender() {
    this._dom = {
      titleElement: this.shadowRoot.querySelector('.title'),
      descriptionElement: this.shadowRoot.querySelector('.description'),
      crossIconElement: this.shadowRoot.querySelector('.cross-icon'),
    };
  }

  /**
   * @private
   * @function _renderTime
   * @description Render time
   */
  _renderTime = () => {
    if (this.timestamp == null) {
      return nothing;
    }
    return html`<span class="notification-log-item-time">${this._calcTimeDiff()}</span>`;
  }

  /**
   * @private
   * @function render
   * @description Render component
   */
  render() {
    return html`
      <div class="notification-log-item" @click="${this}">
        <div class="title">
          ${this.title}
        </div>
        <div class="description">
          ${this.description}
        </div>
        ${this._renderTime()}
        <eui-v0-icon class="cross-icon" name="cross"></eui-v0-icon>
      </div>
    `;
  }
}

NotificationLogPanelItem.register();
